@import url("https://fonts.googleapis.com/css?family=Montserrat&display=swap");

:root {
  --pink: rgba(238, 80, 157, 1);
  --light-blue: rgba(0, 175, 219, 1);
  --dark-blue: rgba(0, 107, 180, 1);
  --grey: rgba(87, 87, 87, 1);
  --black: rgba(0, 0, 0, 0);
  --off-white: rgba(239, 239, 233);
  --white: rgba(255, 255, 255, 1);
}

* {
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

.container {
  display: flex;
  flex-flow: wrap;
  width: 90vw;
  margin: 0 auto;
}

.cards {
  margin-top: 60px;
}

.chart {
  margin-top: 150px;
}

.beer-container {
  position: absolute;
  top: 60px;
}

.barchart-container {
  position: relative;
  width: 100px;
  display: flex;
  align-content: center;
  margin: 2px;
  padding: 2px;
  height: 20px;

  .barchart-label {
    z-index: 2;
    color: var(--dark-blue);
  }

  .barchart-data {
    position: absolute;
    background-color: var(--pink);
    height: 100%;
  }
}

.compare-beers {
  margin-top: 60px;
  flex-flow: wrap;
  justify-content: flex-start;
  align-content: flex-start;
}

.beer-profile {
  display: flex;
  flex-flow: column;
  border: 1px dashed var(--light-blue);
  margin: 10px;
  padding: 2px;
  width: 300px;
  max-width: 300px;

  .cell {
    min-width: 200px;
    max-height: 200px;
    max-width: 300px;
    padding: 2px;

    .img {
      height: 150px;
    }

    .info {
      display: flex;
      justify-content: flex-end;
      font-weight: 900;
      font-size: 35px;
      padding: 0;
      color: var(--light-blue);
    }
  }
}

.actions {
  position: fixed;
  z-index: 1;
  justify-content: space-evenly;
  background: white;
  top: 0;
  height: 4em;
  padding: 0.7em;
}

.col {
  display: flex;
  flex-flow: column;
}

.row {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
}

.beer-card {
  position: relative;
  padding: 4px;
  border: none;

  &:hover {
    cursor: pointer;
  }

  .img {
    position: absolute;
    left: 70px;
    top: 20px;
    height: 150px;
  }

  .footer {
    position: absolute;
    bottom: 0;
    left: 10px;
    width: 167px;
    margin: 0 auto;
    color: white;
    background: var(--light-blue);
    padding: 10px;
  }
}

.pagination {
  .input,
  .btn {
    height: 2.5em;
    margin: 6px 0;
  }

  .input {
    color: var(--dark-blue);
  }
}

.error {
  line-height: 3em;
  font-size: 18px;
  color: #c0392b;
}

input[type="checkbox"] + label {
  display: block;
  margin: 0.2em;
  cursor: pointer;
  padding: 0.2em;
}

input[type="checkbox"] {
  position: absolute;
  display: none;
}

input[type="checkbox"] + label:before {
  content: "\2714";
  border: 0.1em solid var(--light-blue);
  border-radius: 0.2em;
  display: inline-block;
  width: 10em;
  height: 10em;
  padding-left: 0.2em;
  padding-bottom: 0.3em;
  margin-right: 0.2em;
  vertical-align: bottom;
  color: transparent;
  transition: 0.2s;
}

input[type="checkbox"] + label:active:before {
  transform: scale(0);
}

input[type="checkbox"]:checked + label:before {
  background-color: var(--light-blue);
  border-color: var(--light-blue);
  color: #fff;
}

input[type="checkbox"]:disabled + label:before {
  transform: scale(1);
  border-color: #aaa;
}

input[type="checkbox"]:checked:disabled + label:before {
  transform: scale(1);
  background-color: var(--dark-blue);
  border-color: var(--dark-blue);
}

.action-radios {
  display: flex;
  flex-flow: wrap;
}

input[type="radio"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: inline-block;
  position: relative;
  background-color: #f1f1f1;
  color: #666;
  top: 10px;
  height: 30px;
  width: 30px;
  border: 0;
  border-radius: 50px;
  cursor: pointer;
  margin-right: 7px;
  outline: none;

  &:checked::before {
    position: absolute;
    font: 13px/1 "Open Sans", sans-serif;
    left: 11px;
    top: 7px;
    content: "\02143";
    transform: rotate(40deg);
  }

  &:hover {
    background-color: #f7f7f7;
  }

  &:checked {
    background-color: #f1f1f1;
  }
}

.radio-label {
  font: 15px/1.7 "Open Sans", sans-serif;
  color: #333;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: pointer;
}

.btn {
  border: none;
  color: white;
  background: var(--light-blue);

  &:hover {
    cursor: pointer;
    color: var(--dark-blue);
    background: white;
    border: 1px solid var(--light-blue);
  }

  &:disabled {
    background: #aaa;
  }
}

.select {
  width: 200px;
  border: none;
  border-bottom: 1px solid var(--light-blue);
  margin: 6px;
  padding: 4px;
  background: var(--light-blue);
  color: white;

  &:hover {
    background: white;
    color: var(--dark-blue);
  }

  .option {
    height: 2em;
  }
}

.link {
  color: white;
  font-size: 15px;
  font-weight: bold;
  margin: 6px;

  &:hover {
    color: white;
  }
}

.input {
  padding: 0 6px;
}

progress {
  transform: rotate(180deg);
}

.group {
  width: 100px;
}

.name {
  padding: 3px;
  margin: 3px;
  font-weight: bold;
}

.inspect {
  width: 20px;
  height: 20px;
  text-align: center;

  &:hover {
    color: var(--light-blue);
    background: white;
    border-radius: 20%;
  }
}

.tagline {
  quotes: "“" "”" "‘" "’";
  text-align: center;

  &::before {
    content: open-quote;
  }

  &::after {
    content: close-quote;
  }
}

.modal {
  position: fixed;
  margin: 0 auto;
  z-index: 1;
  background: white;
  transform: translate(25em, 1em);
  box-shadow: 0 8px 6px -6px var(--light-blue);
}
